@import url(https://fonts.googleapis.com/css?family=Assistant:300,400,600);
.frame {
  height: 100vh;
  overflow: hidden;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

html {
  background-color: black;
}

.frame img {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

h1 {
  font-size: 80px;
}

h2 {
  font-size: 44px;
}

.centerContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 30px;
}

.buttonContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.frame a {
  background-color: rgba(0, 0, 0, 0.8);
  padding: 10px 20px;
  font-size: 22px;
  color: white;
  border-radius: 8px;
  border: 2px dashed rgba(255, 255, 255, 0.9);
  transition: background-color 0.2s ease;
  margin: 24px 20px 0 20px;
}

.frame a:hover {
  background-color: rgba(0, 20, 80, 0.7);
}

@media screen and (max-width: 800px) {
  .frame:first-child {
    height: calc(100vh - 80px);
  }
  .frame h2 {
    font-size: calc(24px + 3vmin) !important;
  }
}

.project {
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.05);
  margin-bottom: 40px;
  padding: 50px 25px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
}

.project:nth-of-type(2n) {
  flex-direction: row-reverse;
}

.project .image {
  position: relative;
  width: max(40%, 300px);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  margin: 0 25px;
}

/* .iframeWrapper {
  position: relative;
  padding-bottom: 56.25% !important;
  height: 0;
} */

.youtube {
  max-height: 400px;
}

/* .project:nth-of-type(2n) img {
    margin-left: 0;
    margin-right: 70px;
    } */

.info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 300px;
  flex: 1 1;
  margin: 0 25px;
}

.project h2 {
  font-size: 25px;
  margin-bottom: 30px;
  text-align: center;
}

.project h3 {
  text-indent: 40px;
  font-size: 19px;
  font-weight: 400;
  margin-bottom: 20px;
  opacity: 0.5;
}

.linksContainer {
  display: flex;
  flex-direction: row;
}

.linksContainer a {
  color: black;
  padding: 14px 24px;
  margin: 15px;
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.07);
  transition: box-shadow 0.1s ease;
  white-space: nowrap;
  border-radius: 8px;
  margin-bottom: 40px;
  font-size: 18px;
}

.linksContainer a:hover {
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.07);
}

.github {
  padding-right: 56px !important;
  background-size: 26%;
  background-repeat: no-repeat;
  background-position: 83% center;
}

@media screen and (max-width: 800px) {
  .image {
    width: min(80%, 400px) !important;
  }
  .info {
    min-width: 200px;
    width: 100%;
  }
  .project h3 {
    font-size: calc(12px + 1vmin);
  }
  .content {
    padding-left: 20px !important;
    padding-right: 20px !important;
  }
  .project {
    margin-bottom: 20px;
    padding: 30px 16px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.07);
  }
  .youtube {
    min-height: min(40vw, 225px) !important;
    height: min(40vw, 225px) !important;
  }
}

.page iframe {
  width: 100%;
  height: 100vh;
}

.masonry img {
  /* transition: opacity 0.2s ease; */
  border-radius: 0px;
  cursor: pointer;
}

.fullScreen {
  position: fixed;
  top: 0;
  left: 220px;
  right: 0;
  bottom: 0;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 999999999;
  -webkit-backdrop-filter: blur(12px);
          backdrop-filter: blur(12px);
  transition: opacity 0.2s ease;
}

.fullScreen .exit {
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
  padding: 4px 10px;
  border-bottom-right-radius: 8px;
  background-color: rgba(255, 255, 255, 0.7);
}

.fullScreen .half {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  cursor: w-resize;
}

.fullScreen .half:last-of-type {
  left: 50%;
  cursor: e-resize;
}

.fullScreenImage {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

@media screen and (max-width: 800px) {
  .fullScreen {
    left: 0;
  }
  .content {
    padding: 0 14px !important;
  }
}

/* @media screen and (max-width: 340px) {
  .content {
    padding: 0 20px !important;
  }
} */

.content {
  padding-bottom: 40px !important;
}

.gearContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.product {
  padding: 30px 60px;
  box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.07);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  color: black;
  transition: transform 0.2s ease;
  flex: 1 1;
}

.product:hover {
  transform: scale(1.03);
}

.product h2 {
  font-size: 28px;
  font-weight: 400;
  margin-bottom: 20px;
  white-space: nowrap;
}

.product img {
  height: 200px;
}

@media screen and (max-width: 450px) {
  .gear .content {
    padding: 0 20px 40px 20px !important;
  }
  .product {
    width: 100%;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.07);
    margin: 10px;
  }
  .product h2 {
    font-size: calc(14px + 3vw);
  }
  .product img {
    width: 60vw;
    height: auto;
  }
}



* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  font-family: "Assistant", sans-serif;
  font-weight: 600;
}

a {
  text-decoration: none !important;
}

#root {
  width: 100vw;
  overflow-x: hidden;
}

header {
  position: fixed;
  padding: 30px 0;
  padding-bottom: 30px;
  background-color: rgba(0, 126, 204, 0.8);
  color: white;
  overflow-y: scroll;
  width: 220px;
  height: 100vh;
  z-index: 99999;
}
header.dark {
  background-color: rgba(0, 0, 0, 0.5);
}

header h1 {
  color: white;
  padding: 5px 0;
  border: 2px dashed rgba(255, 255, 255, 0.2);
  font-size: 28px;
  max-width: 180px;
  margin: 0 auto;
  text-align: center;
  /* background-color: blue !important; */
}

header h2 {
  font-size: 14px;
  text-align: center;
  line-height: 20px;
  margin-top: 30px;
  margin-bottom: 20px;
  opacity: 0.5;
}

h2 span {
  padding: 0 3px;
  display: block;
}

nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  /* border: 1px solid red; */
}

header.transitionable {
  transition: height 0.2s ease, transform 0.4s ease;
}

.invisible {
  opacity: 0;
  pointer-events: none;
}

.navSection {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* border: 1px solid white; */
  padding: 10px 10px;
  height: auto;
  flex: 1 1;
}

nav a {
  color: white;
  opacity: 0.7;
  transition: opacity 0.2s ease;
  font-size: 18px;
  font-weight: 400;
  padding: 2px 0;
}

nav a:hover {
  opacity: 1;
}

nav h3 {
  font-size: 20px;
  margin-bottom: 10px;
}

.page {
  left: 220px;
  right: 0;
  width: calc(100% - 220px);
  animation: fadeIn 1s ease;
}

.content {
  padding: 0 40px;
}

.page h1 {
  text-align: center;
  font-size: 36px;
  margin-top: 20px;
  margin: 60px 0;
  font-weight: 400;
}

#hamburger {
  /* border: 1px solid red; */
  width: 52px;
  height: 48px;
  padding: 16px;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  z-index: 9999999999;
}

.rotated {
  transform: rotate(90deg);
}

#hamburger .line {
  height: 2px;
  width: 100%;
  background-color: white;
  border-radius: 100px;
}

#hamburger:hover {
  opacity: 1;
}

.madeBy {
  margin-top: 10px;
  padding: 0 30px;
  padding-bottom: 0px;
  opacity: 0.2;
  font-size: 14px;
  text-align: center;
}

.madeBy a {
  opacity: 1;
  color: white;
}

.madeBy img {
  position: static;
  width: 18px;
  height: 18px;
  margin-left: 4px;
  margin-top: 4px;
}

@media screen and (max-width: 800px) {
  header {
    padding: 0;
    padding: 10px 10px;
    width: 100%;
    height: 80px;
    overflow-y: hidden;
    /* height: auto; */
    -webkit-backdrop-filter: blur(12px);
            backdrop-filter: blur(12px);
  }
  header h1 {
    font-size: 22px;
    margin-bottom: 7px;
    padding: 2px 0;
    width: 160px;
  }
  header h2 {
    font-size: 12px;
    padding: 0;
    margin-bottom: 10px;
    line-height: 14px;
    margin-top: 7px;
  }
  header h2 span {
    display: inline;
  }
  nav h3 {
    font-size: 18px;
  }
  nav a {
    font-size: 16px;
  }
  #hamburger {
    opacity: 0.5;
    pointer-events: all;
    transition: transform 0.2s ease, opacity 0.2s ease;
  }
  .page {
    left: 0;
    width: 100%;
    padding-top: 80px;
  }
  .madeBy {
    margin-top: 4px;
    font-size: 12px;
    padding: 0;
  }
  .madeBy img {
    width: 16px;
    height: 16px;
  }
}

@media screen and (max-width: 470px) {
  header h2 {
    display: none;
  }
  header h1 {
    font-size: 24px;
    padding: 6px 0;
    margin-top: 6px;
  }
  #hamburger {
    height: 80px;
    width: 60px;
    padding: 32px 20px;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

